      const linkEditor = await waitForElement(() => queryByTestId('link-editor'));
      const { activeElement } = linkEditor.ownerDocument || {};
      expect(activeElement && activeElement.nodeName === 'INPUT' && activeElement.name === 'href').toBe(true);
