    test('选中链接后复制，粘贴的卡片有标题', async () => {

      const value = ((
        <value>
          <document>
            <block type="paragraph">
              <inline key="link" type="link" data={{ href: 'http://www.baidu.com', cardInfo: { displayType: 'card' } }}>
                {'dingtalk'}
                <anchor />
              </inline>
              <cursor />
            </block>
          </document>
        </value>
      ) as unknown) as Value;

      const { queryByTestId, editor } = await renderEditor(
        <Editor defaultValue={value} />,
      );
      const setData = jest.fn();
      const link = await waitFor(() => queryByTestId('link-card'));
      fireEvent.click(link as HTMLElement);
      fireEvent.copy(editor, {
        nativeEvent: {
          clipboardData: {
            setData,
          },
          preventDefault() { },
        },
      });
      const fragment = transferUtils.decodeFragment(setData.mock.calls[0][1])
      const fragmentString = JSON.stringify(fragment);
      // 从 fragment 的字符串中找到 text 节点的内容
      const isContainsTitle = fragmentString.indexOf('dingtalk') > 0;
      expect(isContainsTitle).toEqual(true);
    });
